jQuery(function($) {
    $(document).ready(function() {
        $('.unique').sameHeight();

        const screenRatio = verge.aspect(screen);
        if (screenRatio >= 1.5) {
            let elemWidth = verge.viewportW(),
                elemHeight = (elemWidth / 3) * 2;
            $('#winning').css('min-height', elemHeight);
        }
    });
});
